import './Qualification.css';
import React from 'react';
// eslint-disable-next-line import/no-extraneous-dependencies
import PropTypes from 'prop-types';
import Card from '../../components/Card';
import SectionTitle from '../../components/SectionTitle';
import ikhsanImage2 from '../../assets/images/ikhsan2.png';
import { qualifications } from '../../data';
import {
  CreativeIcon,
  AnalyticalIcon,
  ProblemSolvingIcon,
  ColaborativeIcon
} from '../../assets/icons';

const cardIconComponent = (id) => {
  if (id === 'creative') return <CreativeIcon />;
  if (id === 'analytical') return <AnalyticalIcon />;
  if (id === 'problem-solving') return <ProblemSolvingIcon />;
  return <ColaborativeIcon />;
};

function Qualification({ id, className }) {
  return (
    <section id={id} className={className}>
      <div className="container">
        <SectionTitle title="Why Hire Me" preTitle="Qualifications" />
        <div className="qualification-container">
          <div className="qualification-image" data-aos="fade-left">
            <img src={ikhsanImage2} alt="qualification" />
          </div>
          <div className="qualification-items">
            {
              qualifications.map((value) => (
                <Card
                  key={value.id}
                  className="qualification-card"
                  id={value.id}
                  title={value.title}
                  text={value.text}
                >
                  { cardIconComponent(value.id) }
                </Card>
              ))
            }
          </div>
        </div>
      </div>
    </section>
  );
}

Qualification.propTypes = {
  id: PropTypes.string,
  className: PropTypes.string
};

Qualification.defaultProps = {
  id: '',
  className: ''
};

export default Qualification;
