import './Expertise.css';
import React from 'react';
// eslint-disable-next-line import/no-extraneous-dependencies
import PropTypes from 'prop-types';
import Card from '../../components/Card';
import SectionTitle from '../../components/SectionTitle';
import ikhsanImage from '../../assets/images/ikhsan.png';
import {
  WebDesignIcon,
  BackEndIcon,
  FrontEndIcon,
  MobileAppIcon
} from '../../assets/icons';
import { expertise } from '../../data';

const cardIconComponent = (id) => {
  if (id === 'back-end') return <BackEndIcon />;
  if (id === 'front-end') return <FrontEndIcon />;
  if (id === 'mobile-app') return <MobileAppIcon />;
  return <WebDesignIcon />;
};

function Expertise({ id, className }) {
  return (
    <section id={id} className={className}>
      <div className="container">
        <SectionTitle title="What I Offer" preTitle="Specialized In" />

        <div className="expertise-container">
          <div className="expertise-image">
            <img src={ikhsanImage} alt="expertise profile" />
          </div>

          <div className="expertise-items">
            {
              expertise.map((value) => (
                <Card
                  key={value.id}
                  className="expertise-card"
                  id={value.id}
                  title={value.title}
                  text={value.text}
                >
                  { cardIconComponent(value.id) }
                </Card>
              ))
            }
          </div>
        </div>
      </div>
    </section>
  );
}

Expertise.propTypes = {
  id: PropTypes.string,
  className: PropTypes.string
};

Expertise.defaultProps = {
  id: '',
  className: ''
};

export default Expertise;
