import './Navbar.css';
import React, { useEffect } from 'react';
// eslint-disable-next-line import/no-extraneous-dependencies
import PropTypes from 'prop-types';
import useWindowSize from '../../hooks/window-size-hooks';
import DrawerButton from '../DrawerButton';

const onClickBurger = (event) => {
  event.stopPropagation();
  const menu = document.querySelector('.nav-mobile');
  if (menu) menu.classList.toggle('open');
};

const onClickClose = (event) => {
  event.stopPropagation();
  const menu = document.querySelector('.nav-mobile');
  if (menu) menu.classList.remove('open');
};

const navMenus = (menus) => (
  <ul className="nav-menu">
    {
      menus.map((menu) => (
        <li className="nav-item" key={menu.link}>
          <a
            className="nav-link"
            href={menu.link}
          >
            {menu.title}
          </a>
        </li>
      ))
    }
  </ul>
);

const navMobile = (menus, close) => (
  <nav className="nav-mobile">
    <DrawerButton
      className="close-button"
      icon="fa-times"
      onClick={close}
    />
    { navMenus(menus) }
  </nav>
);

function Navbar({ menus, mainContent }) {
  const windowSize = useWindowSize();

  useEffect(() => {
    if (windowSize.isMobile) {
      const links = document.querySelectorAll('.nav-link');
      links.forEach((link) => {
        link.addEventListener('click', onClickClose);
      });
    }
  }, [windowSize]);

  return (
    <>
      <a href={mainContent} className="skip-to-content">Skip to content.</a>
      <header className="header-app">
        <nav className="nav">
          <div className="nav-container">
            <div className="nav-logo">
              <a href="/">
                Ikhsan Fauji
              </a>
            </div>
            <div className="nav-burger">
              <DrawerButton
                className="burger-button"
                icon="fa-bars"
                onClick={onClickBurger}
              />
            </div>
            {!windowSize.isMobile ? navMenus(menus) : <></>}
          </div>
        </nav>

        {windowSize.isMobile ? navMobile(menus, onClickClose) : <></>}
      </header>
    </>
  );
}

Navbar.propTypes = {
  // eslint-disable-next-line react/forbid-prop-types
  menus: PropTypes.array.isRequired,
  mainContent: PropTypes.string.isRequired
};

export default Navbar;
