import './Projects.css';
import React from 'react';
// eslint-disable-next-line import/no-extraneous-dependencies
import PropTypes from 'prop-types';
import SectionTitle from '../../components/SectionTitle';
import ProjectCard from '../../components/ProjectCard';

import project1 from '../../assets/projects/project_1.png';
import project2 from '../../assets/projects/project_2.png';

const projects = [
  {
    image: project1,
    alt: 'project 1',
    detailLink: '/',
    appLink: '/'
  },
  {
    image: project2,
    alt: 'project 2',
    detailLink: '/',
    appLink: '/'
  },
  {
    image: project1,
    alt: 'project 3',
    detailLink: '/',
    appLink: '/'
  },
  {
    image: project2,
    alt: 'project 4',
    detailLink: '/',
    appLink: '/'
  }
];

function Projects({ id, className }) {
  return (
    <section id={id} className={className}>
      <div className="container">
        <SectionTitle title="Selected Projects" preTitle="My Portfolio" />
        <div className="project-container">
          <div className="project-items">
            {
              projects.map((project) => (
                <ProjectCard
                  key={project.alt}
                  image={project.image}
                  alt={project.alt}
                  detailLink={project.detailLink}
                  appLink={project.appLink}
                  animation={project.animation}
                />
              ))
            }
          </div>
        </div>
      </div>
    </section>
  );
}

Projects.propTypes = {
  id: PropTypes.string,
  className: PropTypes.string
};

Projects.defaultProps = {
  id: '',
  className: ''
};

export default Projects;
